import React, { Component } from 'react';

import './App.css';
import Currentcost from "./components/Currentcost";
import Description from "./components/Description";
import Information from "./components/Information";
import Newsfeed from "./components/Newsfeed";


class App extends Component {

    constructor(props){
        super(props);
        this.state = {
            coinChoice: {},
            value : ''
        };
    }

    componentWillMount(){
        this.getCoins();
    }

    getCoins(){
        this.setState({
            coinChoice:{
                data: ['BTC', 'ETH', 'XRP', 'BCH','LTC']
            }
        });
    }

    render() {
        return (
            <div className="App">

                <div>

                    <table>

                        <tr>
                            <td>
                                <h1 className='title crypto'>Crypto-Currency Headline Article </h1>
                                <Newsfeed />
                            </td>
                        </tr>

                        <tr>
                            <td>
                                <h1 className='title crypto'>Five Well-Known Crypto-Currencies</h1>
                                <Information />
                            </td>
                        </tr>

                        <tr>
                            <td>
                                <h1 className='title crypto'>Daily Market Ratings (USD)</h1>
                                <Currentcost data={this.state.coinChoice.data} />
                            </td>
                        </tr>

                        <tr>
                            <td>
                                <Description />
                            </td>
                        </tr>

                    </table>
                </div>

            </div>
        );
    }
}

export default App;



