import React, { Component } from 'react';
import axios from 'axios';
var NumberFormat = require('react-number-format');

class Newsfeed extends Component {



    constructor(props) {
        super(props);

        this.state = {
            cryptos: [],
            title: '',
            source: '',
            url: '',
            body: ''
        };
    }

    componentDidMount() {
        axios.get('https://min-api.cryptocompare.com/data/v2/news/?lang=EN')
            .then(res => {
                const cryptos = res.data;
                console.log(cryptos);
                this.setState({cryptos: cryptos});

                this.setState({title: cryptos.Promoted[0].title});
                this.setState({body: cryptos.Promoted[0].body});
                this.setState({source: cryptos.Promoted[0].source});
                this.setState({url: cryptos.Promoted[0].url});

            })


    }

    render() {
        return (
            <div className="Newsfeed">

                <table className="newsfeed">
                    <tr>
                        <td>
                            <h3>{this.state.title} </h3>
                            <h6>Source: <a href= {this.state.url} target="_blank">{this.state.source}</a></h6>

                        </td>
                    </tr>

                    <tr>
                        <td><p className='articletext'>{this.state.body}</p></td>
                    </tr>

                </table>

            </div>
        );
    }
}

export default Newsfeed;


