import React, {Component} from 'react';

class Information extends Component{
    constructor(props){
        super(props);
    }

    render(){

        return (
            <div className="Information">

                <table >

                    <tr>
                        <th></th>
                        <th>Title</th>
                        <th>Ticker</th>
                        <th>Website</th>
                        <th>Type</th>
                        <th>Supply</th>
                        <th>Initiation Date</th>
                        <th>Block Time</th>
                    </tr>

                    <tr>
                        <th><h6 className="desc">One</h6> </th>
                        <td><h6 className="sectxt desc">Bitcoin</h6> </td>
                        <td><h6 className="sectxt desc">BTC</h6></td>
                        <td><h6 className="sectxt desc"><a href= 'https://bitcoin.org/' target="_blank">bitcoin.org</a></h6></td>
                        <td><h6 className="sectxt desc">Block</h6></td>
                        <td><h6 className="sectxt desc">21,000,000</h6></td>
                        <td><h6 className="sectxt desc">January 3, 2009</h6></td>
                        <td><h6 className="sectxt desc">10 Minutes</h6></td>
                    </tr>

                    <tr>
                        <th><h6 className="desc">Two</h6> </th>
                        <td><h6 className="sectxt desc">Ethereum</h6> </td>
                        <td><h6 className="sectxt desc">ETH</h6></td>
                        <td><h6 className="sectxt desc"><a href= 'https://www.ethereum.org/' target="_blank">ethereum.org</a></h6></td>
                        <td><h6 className="sectxt desc">Block and Uncle/Ommer</h6></td>
                        <td><h6 className="sectxt desc">98,316,894</h6></td>
                        <td><h6 className="sectxt desc">July 30, 2015</h6></td>
                        <td><h6 className="sectxt desc">15 Seconds</h6></td>
                    </tr>

                    <tr>
                        <th><h6 className="desc">Three</h6> </th>
                        <td><h6 className="sectxt desc">Ripple</h6> </td>
                        <td><h6 className="sectxt desc">XRP</h6></td>
                        <td><h6 className="sectxt desc"><a href= 'https://ripple.com/' target="_blank">ripple.com</a></h6></td>
                        <td><h6 className="sectxt desc">Block</h6></td>
                        <td><h6 className="sectxt desc">100,000,000,000 </h6></td>
                        <td><h6 className="sectxt desc">2012</h6></td>
                        <td><h6 className="sectxt desc">3.5 Seconds</h6></td>
                    </tr>

                    <tr>
                        <th><h6 className="desc">Four</h6> </th>
                        <td><h6 className="sectxt desc">Bitcoin Cash</h6> </td>
                        <td><h6 className="sectxt desc">BHC</h6></td>
                        <td><h6 className="sectxt desc"><a href= 'https://www.bitcoincash.org/' target="_blank">bitcoincash.org</a></h6></td>
                        <td><h6 className="sectxt desc">Block</h6> </td>
                        <td><h6 className="sectxt desc">21,000,000  </h6></td>
                        <td><h6 className="sectxt desc">January 3, 2009 </h6></td>
                        <td><h6 className="sectxt desc">10 minutes </h6></td>
                    </tr>

                    <tr>
                        <th><h6 className="desc">Five</h6> </th>
                        <td><h6 className="sectxt desc">Litecoin</h6> </td>
                        <td><h6 className="sectxt desc">LTC</h6></td>
                        <td><h6 className="sectxt desc"><a href= 'https://litecoin.org/' target="_blank">litecoin.org</a></h6></td>
                        <td><h6 className="sectxt desc">Block</h6></td>
                        <td><h6 className="sectxt desc">84,000,000  </h6></td>
                        <td><h6 className="sectxt desc">October 2011 </h6></td>
                        <td><h6 className="sectxt desc">10 minutes </h6></td>
                    </tr>

                </table>








            </div>
        )

    }

}

export default Information;


