import React, {Component} from 'react';
import axios from 'axios';
var NumberFormat = require('react-number-format');



class Currentcost extends Component{

    constructor(props){
        super(props);
        this.state = {
            cryptosUS:[],
            cryptosEU:[],
            coinChoice: props.coinChoice,
            BitCoin: '',
            Ethereum: '',
            Ripple: '',
            BitcoinCash: '',
            Litecoin: ''
        };
    }

    static defaultProps = {
        data: "",
    }

    componentWillMount(){
        var valuesUS = 'https://min-api.cryptocompare.com/data/pricemulti?fsyms=' + this.props.data + '&tsyms=USD';
        axios.get(valuesUS)
            .then(res => {
                const cryptosUS = res.data;
                console.log(cryptosUS);
                this.setState({cryptosUS: cryptosUS});
            });

        var valuesEU = 'https://min-api.cryptocompare.com/data/pricemulti?fsyms=' + this.props.data + '&tsyms=EUR';
        axios.get(valuesEU)
            .then(res => {
                const cryptosEU = res.data;
                console.log(cryptosEU);
                this.setState({cryptosEU: cryptosEU});
            });

        axios.get('https://min-api.cryptocompare.com/data/dayAvg?fsym=BTC&tsym=USD')
            .then(res => {
                const cryptos = res.data;
                console.log(cryptos);
                this.setState({cryptos: cryptos});
                this.setState({BitCoin: cryptos.USD});
            })

        axios.get('https://min-api.cryptocompare.com/data/dayAvg?fsym=ETH&tsym=USD')
            .then(res => {
                const cryptos = res.data;
                console.log(cryptos);
                this.setState({cryptos: cryptos});
                this.setState({Ethereum: cryptos.USD});
            })

        axios.get('https://min-api.cryptocompare.com/data/dayAvg?fsym=XRP&tsym=USD')
            .then(res => {
                const cryptos = res.data;
                console.log(cryptos);
                this.setState({cryptos: cryptos});
                this.setState({Ripple: cryptos.USD});
            })

        axios.get('https://min-api.cryptocompare.com/data/dayAvg?fsym=BCH&tsym=USD')
            .then(res => {
                const cryptos = res.data;
                console.log(cryptos);
                this.setState({cryptos: cryptos});
                this.setState({BitcoinCash: cryptos.USD});
            })

        axios.get('https://min-api.cryptocompare.com/data/dayAvg?fsym=LTC&tsym=USD')
            .then(res => {
                const cryptos = res.data;
                console.log(cryptos);
                this.setState({cryptos: cryptos});
                this.setState({Litecoin: cryptos.USD});
            })
    }

    render(){

        return (
            <div className="currentCost">

                <table className="newsfeed">
                    <tr>
                        <td>
                            <table>
                                <tr>
                                    <th className='tabhead'><h5>Current </h5></th>
                                    <th>
                                        <table>
                                            {Object.keys(this.state.cryptosUS).map((key) => (
                                                    <tr>
                                                        <th className="tabhead">{key}: </th>
                                                        <td className='averages'><NumberFormat value={this.state.cryptosUS[key].USD}
                                                                                                       displayType={'text'} decimalPrecision={2}
                                                                                                       thousandSeparator={true} />
                                                        </td>
                                                    </tr>
                                            ))}
                                        </table>
                                    </th>
                                </tr>

                                <tr>

                                </tr>

                            </table>
                        </td>

                        <td>
                            <table>
                                <tr>
                                    <th className="tabhead">
                                        <h5>24 Hour Average </h5>
                                    </th>

                                    <td>

                                        <table className="newsfeed">

                                            <tr>
                                                <th className="tabhead">BTC: </th>
                                                <td className='averages'>
                                            <span><NumberFormat value={this.state.BitCoin}
                                                                displayType={'text'} decimalPrecision={2}
                                                                thousandSeparator={true} />
                                                        </span>
                                                </td>
                                            </tr>
                                            <tr>
                                                <th className="tabhead">ETH: </th>
                                                <td className='averages'>
                                            <span><NumberFormat value={this.state.Ethereum}
                                                                displayType={'text'} decimalPrecision={2}
                                                                thousandSeparator={true} />
                                                        </span>
                                                </td>
                                            </tr>
                                            <tr>
                                                <th className="tabhead">XRP: </th>
                                                <td className='averages'>
                                            <span><NumberFormat value={this.state.Ripple}
                                                                displayType={'text'} decimalPrecision={2}
                                                                thousandSeparator={true} />
                                                        </span>
                                                </td>
                                            </tr>

                                            <tr>
                                                <th className="tabhead">BCH: </th>
                                                <td className='averages'>
                                            <span><NumberFormat value={this.state.BitcoinCash}
                                                                displayType={'text'} decimalPrecision={2}
                                                                thousandSeparator={true} />
                                                        </span>
                                                </td>
                                            </tr>

                                            <tr>
                                                <th className="tabhead">LTC: </th>
                                                <td className='averages'>
                                            <span><NumberFormat value={this.state.Litecoin}
                                                                displayType={'text'} decimalPrecision={2}
                                                                thousandSeparator={true} />
                                                        </span>
                                                </td>

                                            </tr>

                                        </table>

                                    </td>

                                </tr>
                            </table>
                        </td>

                    </tr>

                </table>

            </div>
        )
    }
}

export default Currentcost;


